nav {
    background: rgb(34, 193, 195);
    background: linear-gradient(0deg, rgba(34, 193, 195, 1) 0%, rgba(45, 245, 253, 0.9473039215686274) 100%);
    width: 1000%;
}

nav li {
    display: inline-block;
}

nav a {
    padding: 10px;
    /* color:brown; */
    color: rgb(5, 0, 0);
    display: inline-block;
    text-decoration: none;
}

nav ul {
    margin-top: 0px;
}

nav a.active {
    color: black;
    background-color: white;
}