table,
th,
td {
    border: 2px solid rgb(3, 0, 0);
    border-collapse: collapse;
    background: rgb(238, 174, 202);
    background: radial-gradient(circle, rgba(238, 174, 202, 1) 0%, rgba(148, 187, 233, 1) 100%);
    color: black;
}

th {
    padding: 3px;
    background: rgb(63, 94, 251);
    background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 97%, rgba(252, 70, 107, 1) 98%);
    color: rgb(11, 0, 0);
}

td {
    padding: 4px;
}

tr:hover {
    background-color: rgb(119, 10, 10);
}

.buttons button.active {
    padding: 6px;
    background: rgb(63, 94, 251);
    background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 97%, rgba(252, 70, 107, 1) 98%);
    color: rgb(0, 0, 0);
}

.buttons button:disabled {
    opacity: 0.3;
}