.card {
    font-size: large;
    margin-top: 100px;
    margin-left: 50px;
    margin-right: 50px;
    width: 250px;
    display: inline-block;
    background: rgb(63, 94, 251);
    background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);
    padding: 10px;
    padding-left: 10px;
    text-align: center;
    border-radius: 10px;
}

.card .button {
    height: 30px;
    width: 500px;

}