body {
	align-items: center;
	display: inline-table;
	margin: 0px;
	border-style: border-box;
	/* background: #02AAB0; */
	
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #00CDAC, #02AAB0);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #00CDAC, #02AAB0);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	

	font-family: sans-serif;
}

.calculator .container {
	height: 100px;
	width: 70px;
	display: flex;
}

.calculator {
	background-color: RGB(214, 107, 12);
	text-align: justify;
	margin-top: 125px;
	margin-right: 340px;
	margin-left: 650px;
	padding: 10px;
	padding-left: 10px;
	padding-right: 30px;
	border-radius: 10px;
	/* box-shadow: inset 15px 15px 10px rgba(87, 189, 220, 0.597),
	                  15px 15px 20px rgba(87, 189, 220, 0.597); */
	display: grid;
	grid-template-columns: repeat(4, 60px);
}

.calculator input {
	grid-column: span 4;
	height: 60px;
	width: 220px;
	background-color: rgb(224, 196, 125);
	box-shadow: inset -5px -5px 12px rgba(30, 3, 3, 0.349),
		inset 5px 5px 12px rgba(0, 0, 0, 0.501);
	border: none;
	border-radius: 10px;
	text-align: end;
	font-size: 40px;
	margin: auto;
	margin-top: 30px;
	margin-left: 10px;
	margin-bottom: 30px;
	padding: 10px;
}

.calculator button {
	height: 40px;
	width: 50px;
	background-color: RGB(247, 185, 129);
	box-shadow: inset -1px -1px 5px RGB(132, 105, 78),
		inset 5px 5px 12px RGB(132, 105, 78);
	border: none;
	border-radius: 10px;
	margin: 8px;
	font-size: 20px;
}

.calculator .equal {
	width: 110px;
	border-radius: 40px;
	background-color: RGB(247, 185, 129);

}